


















































































import Breadcrumb from '@/components/Breadcrumb/index.vue'
import Hamburger from '@/components/Hamburger/index.vue'
import LangSelect from '@/components/LangSelect/index.vue'
import ThemePicker from '@/components/ThemePicker/index.vue'
import { Component, Vue } from 'vue-property-decorator'
import { AppModule } from '@/store/modules/app'
import { UserModule } from '@/store/modules/user'
import { Form as ElForm } from 'element-ui'
import { changePassword } from '@/api/login'

interface Password {
  password: string;
  newPassword: string;
}

@Component({
  components: {
    Breadcrumb,
    Hamburger,
    LangSelect,
    ThemePicker
  }
})
export default class Navbar extends Vue {
  /** dialog */
  private isShowChangePassword: boolean = false
  /**  */
  private form: Password = {
    password: '',
    newPassword: ''
  }
  /** label */
  private formLabelWidth: string = '120px'
  /** form */
  private rules: any = {
    password: [
      { required: true, message: '', trigger: 'blur' },
      { min: 3, max: 32, message: ' 3  32 ', trigger: 'blur' }
    ],
    newPassword: [
      { required: true, message: '', trigger: 'blur' },
      { min: 3, max: 32, message: ' 3  32 ', trigger: 'blur' }
    ]
  }
  get sidebar() {
    return AppModule.sidebar
  }

  get avatar() {
    return UserModule.avatar
  }

  get device() {
    return AppModule.device
  }

  private toggleSideBar() {
    AppModule.ToggleSideBar(false)
  }

  private logout() {
    UserModule.LogOut().then(() => {
      location.reload() // vue-router bug
    })
  }

  /**  */
  private handleChangePassword(formName: string): void {
    (this.$refs[formName] as ElForm).validate((valid) => {
      if (valid) {
        changePassword(this.form).then(() => {
          this.isShowChangePassword = false
        })
      } else {
        console.log('error submit!!')
        return false
      }
    })
  }
}
